<template lang="pug">
  .techIntro
    h4.name 
    img.img(src="https://firebasestorage.googleapis.com/v0/b/storesense-1a3d1.appspot.com/o/stock%2Fdetail%2Fsticky.png?alt=media&token=f9fad6a3-6f19-464a-a42b-c74f9814c3b4")
    .des
      .title 
      .text K
    .started(@click="toTechOpt()") !
</template>

<script>
export default {
  methods: {
    toTechOpt(){
      this.$router.push({path: '/tech/options'})
    }
  }
}
</script>

<style lang="sass" scoped>
  $colorGray: #2B2B2B
  $colorLightGray: #3B3838
  $colorYellow: #FFC000
  $colorWhite: rgba(white, 0.7)

  *
    // border: solid 1px black
  .techIntro
    color: white
    height: 100%
    .started
      margin-top: 15px
      text-align: center
      padding: 5px 10px
      font-weight: 700
      letter-spacing: 2px
      border: solid 1px $colorYellow
      margin-bottom: 20px
      cursor: pointer
      color: $colorYellow
    .name
      text-align: center
      font-weight: 700
      padding-bottom: 8px
      border-bottom: solid 2px $colorYellow
    .img
      width: 100%
      height: 300px
    .des
      margin-top: 20px
      .title
        font-weight: 700
        text-align: center
        margin-bottom: 10px
        padding-bottom: 8px
        border-bottom: solid 2px $colorYellow
        font-size: 16px
      .text
        padding: 5px 8px
        border: solid 1px $colorWhite
        border-radius: 3px
        letter-spacing: 1px
        font-size: 16px

</style>



